import React, { useState } from "react"
import styled from "styled-components"
import QF from "../components/quickfind"

const search = new QF(100)

var Box = styled.div`
  height: 50px;
  width: 50px;
  box-sizing: border-box;
  border: 1px solid black;
  background-color: ${props => props.color};
`
const boardConstruct = function(n) {
  var result = []
  for (var i = 0; i < n; i++) {
    var random = Math.floor(Math.random() * Math.floor(10))
    result.push(<Box color={random < 6 ? "white" : "black"} />)
  }
  return result
}

var newBoard = boardConstruct(100)

const percolate = function() {
  const [value, setValue] = useState(0)
  newBoard.forEach((box, index) => {
    if (box.props.color === "white") {
      if (index % 10 !== 0 && newBoard[index - 1].props.color !== "black") {
        search.union(index, index - 1)
      }
      if (index > 9 && newBoard[index - 10].props.color !== "black") {
        search.union(index, index - 10)
      }
    }
  })
  newBoard.forEach((box, index) => {
    if (search.root(index) < 10) {
      box.props.color = "blue"
    }
  })
  return () => setValue(value => ++value)
}

const quicksearch = () => {
  const Section = styled.div`
    display: flex;
    flex-wrap: wrap;
    height: 500px;
    width: 500px;
    margin: auto;
    padding-top: 10px;
    @media (max-width: 820px) {
      margin-left: 1em;
      margin-right: 1em;
    }
  `
  return (
    <Section>
      {newBoard}
      <button onClick={percolate}>percolate</button>
    </Section>
  )
}

export default quicksearch
